import React, { Component } from 'react';
import { Route, Switch } from 'react-router-dom';
import { Home, Amulet } from 'pages';

class App extends Component {
  render() {
    return (
      <div>
        <Route exact path="/" component={Home} />
        <Switch>
          <Route path="/amulet/:name" component={Amulet} />
          <Route path="/amulet" component={Amulet} />
        </Switch>
      </div>
    );
  }
}

export default App;