import React from 'react';
import queryString from 'query-string';

import amulet_data from '../res/amulet_data.js';
// import strings from '@res/strings.js';

import './Amulet.css';

const Amulet = ({location, match}) => {
  const query = queryString.parse(location.search);
  const index = query.index ? Number(query.index) : 0;
  
  return (
    <div className="Amulet">
      <header className="Amulet-header">
        <img src={amulet_data[index].amuletImage} className="Amulet-logo" alt="AmuletImage" />
        <p className="Amulet-title">
          {amulet_data[index].amuletTitle}
        </p>
        <p className="Amulet-content">
          {amulet_data[index].amuletContent}
        </p>
        <a
          className="Amulet-link"
          href="https://bit.ly/2kODTkt"
          target="_blank"
          rel="noopener noreferrer"
        >
          '나를 지켜주는 부적' 앱 다운로드
        </a>
      </header>
    </div>
  );
}

export default Amulet;
